<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5
    .titulo-principal.color-acento-contenido(data-aos="fade-right")
      .titulo-principal__numero
        span 3
      h1 Evaluacin financiera y social 

    .row.justify-content-center.align-items-center.mb-4
      .col-md-5.col-8.mb-md-0.mb-4
        figure.mb-5(data-aos="fade-right")
          img(src='@/assets/curso/tema3/img01.png')
      .col-md-7.col-10
        p La evaluacin financiera y social se basa en realizar estimaciones de lo que proyecta el futuro, los beneficios y costos asociados durante un horizonte de tiempo. Al plantearlas el analista debe conocer la actividad econmica a la que se dedica la empresa, el entorno en que se desempea la organizacin, as como el comportamiento de los precios, la ubicacin y acceso a los insumos, prever los avances tecnolgicos, la evolucin de la demanda, el comportamiento de la competencia, cambios de gobierno o de poltica econmica y social, leyes aplicables al sector y dems variables del entorno; todos ellos influyen en las alternativas planteadas por los analistas que seguramente no van a coincidir. 
        p.mb-0 Estas permiten determinar, de manera objetiva, las magnitudes numricas de los resultados a obtener, apoyadas en clculos matemticos donde se validarn supuestos que han tenido origen en la realidad organizacional con el fin de tener una correcta valuacin de los beneficios esperados.

    .row.justify-content-center.align-items-center.mb-4
      .col-md-7.col-10.order-md-1.order-2
        .bloque-texto-b.color-acento-contenido.bloque01.p-4
          .bloque-texto-b__texto(data-aos="fade-right")
            p.mb-0 Como se ha planteado en anteriores apartados de este componente, antes de embarcarse en este viaje de la evaluacin financiera y social es necesario definir el objetivo de estas para tener claro el o los criterios de evaluacin, anotando que ambas tienen marcadas diferencias, pero siempre tendr mayor relevancia la alternativa que se enmarque en la realidad econmica e institucional de una nacin de acuerdo con las ventajas y desventajas cualitativas y cuantitativas que impliquen la designacin de recursos.
      .col-md-3.col-6.order-md-2.order-1.mb-md-0.mb-4
        figure.mb-5(data-aos="fade-left")
          img(src='@/assets/curso/tema3/img02.png')
    
    .row.justify-content-center.align-items-center.cajon.color-acento-contenido.p-4.mb-4
      .col-md-2.col-4(data-aos="fade-right")
        img(src='@/assets/curso/tema3/img03.png')
      .col-md-9.col-11
        p Para realizar la evaluacin financiera y social el inversionista no acude a la informacin que le brinda los estados financieros de la situacin financiera y de resultados que en un proyecto de inversin o una empresa en marcha seran proyecciones de las probabilidades de xito que se podra alcanzar con x alternativa de inversin. En esta parte de la evaluacin financiera y social la herramienta bsica para evaluar una alternativa de inversiones el flujo de caja o de efectivo.

    p Este estado financiero conocido segn las NIIF como flujo de efectivo que en sntesis es la consolidacin de ingresos y egresos para que la organizacin pueda proyectarse acerca de los ingresos que recibir y de los costos y gastos que deber asumir para garantizar la operatividad de la empresa. Como complemento, observe con el siguiente video los elementos a tener presente dentro de la informacin financiera desde el uso de las NIF en las empresas:

    figure.mb-5(data-aos="fade-right")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/duUyd-RoZrU" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
    
    .row.mb-4(data-aos="fade-right")
      .col-auto.BG03.p-3
        h4.mb-0 Flujo de fondos, flujo de efectivo o flujo de caja libre
    
    .row.justify-content-center.align-items-center.mb-4
      .col-md-6.col-11.order-md-1.order-2.mb-md-0.mb-4
        p Este es un estado financiero de mucha importancia para la evaluacin financiera de un proyecto de inversin, ya que es el instrumento que arroja la informacin o resultados sobre los que se determinan los beneficios a recibir por el proyecto, en los que, adems de incluir ingresos y gastos, es necesario incluir la depreciacin, amortizacin del activo nominal, valor residual, utilidades y prdidas.
        p.mb-0 Este ejercicio tiene su grado de complejidad, ya que requiere de la elaboracin de un flujo de acuerdo con su fin; es decir, un flujo de caja para medir la rentabilidad, otro para evaluar los resultados de los recursos propios y el ltimo para definir la capacidad de pago frente a obligaciones financieras; estos tambin difieren si la evaluacin se realiza sobre un proyecto a implementar o sobre una actividad econmica en marcha.
      .col-md-6.col-8.order-md-2.order-1
        figure.mb-5(data-aos="fade-left")
          img(src='@/assets/curso/tema3/img04.png')

    .row.justify-content-center.align-items-center.mb-4
      .col-md-4.col-8.mb-md-0.mb-4
        img(src='@/assets/curso/tema3/img05.png')
      .col-md-6.col-10
        p Los elementos para la construccin de un flujo de caja son cuatro:
        ol.lista-ol--cuadro
          li 
            .lista-ol--cuadro__vineta
              span 1
            | Ingresos iniciales o inversin.
          li 
            .lista-ol--cuadro__vineta
              span 2
            | Ingresos y gastos de operacin.
          li 
            .lista-ol--cuadro__vineta
              span 3
            | Tiempo o momento en el que se reciben los ingresos y se realizan las erogaciones.
          li 
            .lista-ol--cuadro__vineta
              span 4
            | El valor de desecho o salvamento del proyecto de inversin.
        p Para construir el flujo de fondos se necesita conocer el horizonte de evaluacin.

    .row.mb-4(data-aos="fade-right")
      .col-auto.BG03.p-3
        h4.mb-0 Estructura de flujo de caja
    
    p La elaboracin de un flujo de caja se puede realizar teniendo en cuenta esta estructura general, la cual tiene utilidad de acuerdo con el fin de la evaluacin y el objetivo del proyecto, as:

    .row.justify-content-center.align-items-center.mb-4
      .col-md-6.col-10.px-0
        img(src='@/assets/curso/tema3/img06.png')
      .col-md-4.col-10.BG04.p-5
        p.text-bold.mb-0 + Ingresos sujetos a impuestos.
        p.text-bold.mb-0 -	Egresos sujetos a impuestos.
        p.text-bold.mb-0 = Ebitda.
        p.text-bold.mb-0 -	Gastos no desembolsables.
        p.text-bold.mb-0 = Resultado o utilidad antes de impuesto.
        p.text-bold.mb-0 -	Impuestos.
        p.text-bold.mb-0 = Resultado despus de impuesto.
        p.text-bold.mb-0 + Ajustes por gastos no desembolsables.
        p.text-bold.mb-0 = Resultado operacional neto.
        p.text-bold.mb-0 -	Egresos no sujetos a impuestos.
        p.text-bold.mb-0 + Beneficios no sujetos a impuestos.
        p.text-bold.mb-0 = Flujo de caja.

    p Para ello es importante tener en cuenta:

    LineaTiempoE.color-primario.mb-4
      .row.align-items-center(titulo="Ingresos")
        .col-lg-2.col-4
          img(src='@/assets/curso/tema3/img07.png')
        .col-10
          p.mb-0 Ingresos sujetos de impuestos son todos los generados por la venta de productos y la venta de activos cuando se da su reemplazo. 
      .row.align-items-center(titulo="Egresos")
        .col-lg-2.col-4
          img(src='@/assets/curso/tema3/img08.png')
        .col-10
          p Los egresos no sujetos a impuestos son la inversin en terrenos, obras fsicas y maquinaria, as como la ampliacin de la planta o capacidad productiva, la inversin en activos de reemplazo y la inversin en capital de trabajo.
          p En cuanto a los gastos no desembolsables son descontados en el rubro de impuestos, pero que no representan una erogacin de dinero como la depreciacin, la amortizacin de los activos intangibles o el valor de un activo en libros. 
          p.mb-0 Para calcular la depreciacin a pesar de existir diferentes mtodos para calcularla, al construir flujos de caja el criterio para calcular este concepto se basa en el mtodo de lnea recta, es decir, supone que se deprecia todo activo en proporcin similar cada ao por entregar un escenario ms conservador.
      .row.align-items-center(titulo="Costos")
        .col-lg-2.col-4
          img(src='@/assets/curso/tema3/img09.png')
        .col-10
          p.mb-0 Son erogaciones sujetas a impuesto y que corresponden a los costos correspondientes a la produccin o fabricacin de productos a comercializar.

    .row.mb-4(data-aos="fade-right")
      .col-auto.BG03.p-3
        h4.mb-0 Beneficios no sujetos a impuestos son el valor de desecho del proyecto

    p El valor de desecho del proyecto de inversin se refiere al valor que tienen los activos adquiridos en el proyecto por el inversionista para conocer luego de sustraer la depreciacin a una tasa de retorno solicitada el cual se calcula por tres vas o mtodos, a saber:

    .row.justify-content-center.mb-4
      .col-md-4.col-10.mb-md-0.mb-4(data-aos="fade-right")
        .tarjeta-avatar
          img(src='@/assets/curso/tema3/img10.png' alt='AvatarTop')
          .BG15.p-4.color-secundario.pt-5
            h4 Mtodo contable
            p Es un mtodo conservador para calcular el valor de desecho que se utiliza en etapa de perfil o prefactibilidad y se realiza con la sumatoria de todos los valores contables o en libros de cada activo, teniendo en cuenta la inversin del activo, el nmero de aos de depreciacin faltantes y los ya depreciados. 
      .col-md-4.col-10.mb-md-0.mb-4(data-aos="zoom-in")
        .tarjeta-avatar
          img(src='@/assets/curso/tema3/img11.png' alt='AvatarTop')
          .BG15.p-4.color-secundario.pt-5
            h4 Mtodo comercial
            p Se determina segn el valor comercial que se espera que los activos puedan tener y sus efectos tributarios. Se calcula tomando el valor comercial, la utilidad antes de impuestos, la utilidad neta y el valor contable.
      .col-md-4.col-10(data-aos="fade-left")
        .tarjeta-avatar
          img(src='@/assets/curso/tema3/img12.png' alt='AvatarTop')
          .BG15.p-4.color-secundario.pt-5
            h4 Mtodo econmico
            p Estima el valor que un inversionista podra pagar por el proyecto y considera el flujo de caja del ltimo ao al que se le sustrae el valor de la depreciacin y se multiplica por la tasa de retorno exigida. Este es el mtodo ms utilizado y aterrizado para calcular el valor de desecho de un proyecto de inversin.

    .row.mb-4(data-aos="fade-right")
      .col-auto.BG03.p-3
        h4.mb-0 Flujo de caja del inversionista

    p Este flujo de caja nace de la necesidad de conocer la rentabilidad que recibir un inversionista al colocar sus recursos propios en un proyecto de inversin para lo cual es necesario incluir el efecto del financiamiento con el fin de incluir el impacto de la deuda. De esta manera, se incluye el costo de la financiacin separando el valor de la cuota que asume los gastos financieros y el de amortizacin o disminucin del capital de la obligacin financiera.

    .row.justify-content-center.mb-4
      .col-md-6.col-10.px-0(data-aos="fade-right")
        img(src='@/assets/curso/tema3/img13.png')
      .col-md-4.col-10.BG16.p-3(data-aos="fade-left")
        p.text-bold.ms-2.mb-0 + Ingresos sujetos a impuestos.
        p.text-bold.ms-2.mb-0 -Egresos sujetos a impuestos.
        p.text-bold.ms-2.mb-0 = Ebitda.
        p.text-bold.ms-2.mb-0 - Intereses de la obligacin.
        p.text-bold.ms-2.mb-0 - Gastos no desembolsables.
        p.text-bold.ms-2.mb-0 = Resultado antes de impuesto.
        p.text-bold.ms-2.mb-0 - Impuesto.
        p.text-bold.ms-2.mb-0 = Resultado despus de impuesto.
        p.text-bold.ms-2.mb-0 + Ajustes por gastos no desembolsables.
        p.text-bold.ms-2.mb-0 - Egresos no sujetos a impuestos.
        p.text-bold.ms-2.mb-0 + Beneficios no sujetos a impuestos.
        p.text-bold.ms-2.mb-0 + Prstamo.
        p.text-bold.ms-2.mb-0 - Amortizacin de la obligacin.
        p.text-bold.ms-2.mb-0 = Flujo de caja.

    .row.mb-4(data-aos="fade-right")
      .col-auto.BG03.p-3
        h4.mb-0 Flujo de caja de proyectos de inversin en empresa en marcha

    .row.justify-content-center.align-items-center.mb-4
      .col-md-7.col-10.order-md-1
        p El flujo de caja difiere del inversionista, ya que algunos rubros o conceptos ya estn siendo asumidos por la operacin de la empresa como los gastos de remuneracin del conductor de la empresa que viene desarrollando una actividad o proceso en la organizacin.
        p.mb-0 De esta manera, los conceptos que tienen cabida en este estado financiero son las inversiones que se realizan por reemplazo, ampliacin, tercerizacin de procesos y los de abandono, permitiendo identificar los  proyectos de inversin de reemplazo que tienen origen debido a la reduccin del nivel de produccin causando el incremento en los costos productivos por inactividad para la realizacin de mantenimiento asociados a la vida til agotada del equipo y la obsolescencia.
      .col-md-5.col-8.order-md-2.order-1.order-2.mb-md-0.mb-4
        figure.mb-5(data-aos="fade-left")
          img(src='@/assets/curso/tema3/img14.png')

    p Los proyectos de reemplazo especialmente nacen por las siguientes razones:

    .row.justify-content-center.align-items-center.mb-5
      .col-md-8.BG04.p-4
        .row.justify-content-center.align-items-center
          .col-md-2.col-8.p-0.img01.mb-md-0.mb-4
            figure
              img.w-75(src='@/assets/curso/tema3/img15.png')
          .col-md-10.col-12
            ol.lista-ol--cuadro
              li 
                .lista-ol--cuadro__vineta
                  span a
                | Sustitucin de activos sin cambios en los niveles de operacin ni de ingresos.
              li 
                .lista-ol--cuadro__vineta
                  span b
                | Sustitucin de activos con modificaciones en la productividad, ventas e ingresos.
              li 
                .lista-ol--cuadro__vineta
                  span c
                | Requerida sustitucin de activos con o sin cambio en el nivel operativo.

    p.mt-5 Adems de relacionar lo que representa:

    .row.justify-content-center.mb-4
      .col-md-8
        .row.justify-content-center.align-items-center.mb-4
          .col-md-2.col-4.mb-md-0.mb-4
            img(src='@/assets/curso/tema3/img16.png')
          .col-md-10
            h4 La ampliacin
            p Surge cuando la tecnologa vigente tiene poca capacidad, o para complementar e incrementar los niveles de produccin.
        hr
        .row.justify-content-center.align-items-center.mb-4
          .col-md-2.col-4.mb-md-0.mb-4
            img(src='@/assets/curso/tema3/img17.png')
          .col-md-10
            h4 La tercerizacin
            p Trae beneficios al permitir la concentracin de esfuerzos, compartir riesgos de inversin con proveedores, liberar recursos de compromisos para dedicarlos a nuevas actividades, generar ingresos por venta de activos e incrementar la eficiencia al entregar actividades de la organizacin a expertos.
        hr
        .row.justify-content-center.align-items-center
          .col-md-2.col-4.mb-md-0.mb-4
            img(src='@/assets/curso/tema3/img18.png')
          .col-md-10
            h4 Los proyectos de abandono
            p Se caracterizan por eliminar reas de negocio no rentables liberando recursos para invertirlos en proyectos ms rentables.

    p Para este caso los flujos de caja pueden ser de proyecto puro antes conocido como de la situacin actual sin proyecto y de la situacin con proyecto conocido actualmente como flujo de caja de proyecto puro cuando la inversin por reemplazo se realiza en el ao cero (0), realizados con el fin de evidenciar en los mismo las inversiones en el momento real que se realizan y sus efectos en los flujos de caja, as:

    SlyderF.mb-5(columnas="col-12")
      .row.justify-content-center.BG01.p-4
        .col-md-5.col-10.order-md-1.order-2.mb-4
          h4 Estructura del flujo de caja proyecto puro 
          p.mb-0 = Ingresos sujetos a impuestos*.
          p.mb-0 -	Egresos sujetos a impuestos.
          p.mb-0 -	Gastos no desembolsables.
          p.mb-0 -	Depreciacin.
          p.mb-0 -	Valor en libros.
          p.mb-0 = Resultado/Utilidad antes de impuesto.
          p.mb-0 - Impuesto.
          p.mb-0 = Resultado despus de Impuesto.
          p.mb-0 + Depreciacin.
          p.mb-0 + Valor en libros.
          p.mb-0 = Flujo de caja.
        .col-md-5.col-10.order-md-2.order-1.mb-md-0.mb-4
          img(src='@/assets/curso/tema3/img19.png')
        .col-10.BG16.p-4.order-3
          p * Los ingresos corresponden a la venta de activos que por ser originados por reemplazo se realizara en el ltimo ao del horizonte del proyecto.
      .row.justify-content-center.BG01.p-4
        .col-md-5.col-10.order-md-1.order-2.mb-4
          h4 Estructura del flujo de caja de la situacin con proyecto 
          p.mb-0 = Ingresos sujetos a impuestos*.
          p.mb-0 + Ahorro costos.
          p.mb-0 - Gastos no desembolsables.
          p.mb-0 -	Valor en libros.
          p.mb-0 = Resultado antes de impuesto.
          p.mb-0 - Impuesto.
          p.mb-0 = Resultado despus de impuesto.
          p.mb-0 + Depreciacin.
          p.mb-0 + Valor en libros.
          p.mb-0 = Flujo de caja.
        .col-md-5.col-10.order-md-2.order-1.mb-md-0.mb-4
          img(src='@/assets/curso/tema3/img20.png')
        .col-10.BG16.p-4.order-3
          p * Los ingresos corresponden a la venta del equipo vigente y el impuesto por pagar por la venta que deben ser incorporados en el ao cero (0), porque se realiza la inversin en la compra del activo de reemplazo.
      .row.justify-content-center.BG01.p-4
        .col-md-5.col-10.order-md-1.order-2.mb-4
          p Es pertinente anotar que para tomar decisiones de inversin en el caso del reemplazo se hace necesario comparar a travs de flujo diferencial para evaluar el impacto de la decisin, este se realiza tomando los flujos de caja netos de la situacin sin proyecto o en este caso sin reemplazo y debajo se ubican los flujos de caja netos de la situacin con proyecto o para este caso con reemplazo lo que permite calcular las diferencias y valorar el flujo incremental el cual se construye as:
          h4 Flujo de caja incremental 
          p.mb-0 = Ingresos sujetos a impuestos*.
          p.mb-0 + Ahorro costos.
          p.mb-0 + Gastos no desembolsables/Depreciacin.
          p.mb-0 -	Valor en libros.
          p.mb-0 = Utilidad antes de impuesto.
          p.mb-0 + Impuesto.
          p.mb-0 = Utilidad neta.
          p.mb-0 + Depreciacin.
          p.mb-0 + Valor en libros.
          p.mb-0 -	Inversin.
          p.mb-0 = Flujo de caja.
        .col-md-5.col-10.order-md-2.order-1.mb-md-0.mb-4
          img(src='@/assets/curso/tema3/img21.png')
        .col-10.BG16.p-4.order-3
          p * La financiacin de un proyecto depende de la mejor alternativa que el inversionista debe hallar a partir de la elaboracin de los flujos de costos de cada opcin de financiacin con el fin de elegir las ms apropiada siendo esta la que represente el menor valor actual de costos de capital.

    .row.mb-4(data-aos="fade-right")
      .col-auto.BG03.p-3
        h4.mb-0 Evaluacin financiera
    
    p.mb-5 Al tener un proyecto de inversin se tiene claro que esta se realiza con el objeto de lograr un incremento de la inversin generando valor a la organizacin. Es decir, no es suficiente que al evaluar los flujos de caja anualmente estos sean positivos sin desconocer que muchos de ellos proyectos a emprender en una empresa estn ligados a la incertidumbre de los resultados existen dos instrumentos altamente aceptados para realizar este anlisis en su evaluacin: valor presente neto y tasa interna de retorno. A continuacin, se detalla cmo se presenta la evaluacin financiera.

    LineaTiempoC.color-acento-contenido.mb-4(text-small)
      .row.justify-content-center(titulo="VPN")
        .col-md-10.BG16.p-4.mb-4.mb-md-0
          h4 VPN
          p Se reconoce como el indicador resultado de sustraer del valor presente de los futuros flujos de caja la inversin inicial. Es adecuado precisar que la tasa de inters corresponde a la mnima rentabilidad que deben producir los bienes o activos que posee una empresa conocida tambin como tasa de oportunidad.
          p Tambin puede definirse como Valor Actual Neto o (VAN) que es aceptable cuando es igual o superior a cero, lo cual quiere decir que al restar todos los ingresos y erogaciones el proyecto de inversin no presenta riesgos puesto que no pierde su inversin o lo que es mejor, si el valor es mayor a cero quiere decir que la inversin genera beneficios expresados en la cantidad que arroje como resultado.
          p Si el horizonte del proyecto es de 5 aos el nmero de flujos de caja a descontar son 5 a la tasa de inters definida por la organizacin o el inversionista, su frmula es:
          p <span style="background-color:#FCC15A !important; font-weight: bold; padding: 5px">VPN = - I0 + (F/(1 + i)n) + (F/(1 + i)n) + (F/(1 + i)n) +  </span>
          p Estos son los criterios para decidir con base en VPN:
          p.text-bold 1. Si VPN &gt; 0, entonces el proyecto es aceptable.
          p.text-bold 2. Si VPN &lt; 0, entonces el proyecto no es aceptable.
          p.text-bold 3. Si VPN = 0, entonces es indiferente aceptarlo o no.
      .row.justify-content-center(titulo="TIR")
        .col-md-10.BG16.p-4.mb-4.mb-md-0
          h4 TIR
          p Es un instrumento para medir la rentabilidad que ofrece una inversin y la viabilidad para llevarla a cabo. Definida como la tasa de inters que hace equivalentes los ingresos y egresos tomando como primer egreso la inversin inicial del proyecto, tambin puede definirse como la tasa de inters que equilibra o  iguala en el tiempo los ingresos y egresos de un proyecto. Representa la tasa de inters ms alta que un inversionista puede pagar sin perder su inversin. La frmula es:
          p <span style="background-color:#FCC15A !important; font-weight: bold; padding: 5px">TIR = - I0 +(F/(1 + i)n) + (F/(1 + i)n) + (F/(1 + i)n)</span>
          p Los criterios de seleccin del proyecto de inversin segn TIR, es:
          p.text-bold 1. Si TIR &gt; K, el proyecto ser aceptado. Teniendo una rentabilidad mayor a la tasa mnima esperada o al Costo de Capital (CK).
          p.text-bold 2.Si TIR = K el proyecto ser aceptado si existe la probabilidad de mejorar la posicin en el mercado porque no presenta beneficios o rentabilidad.
          p.text-bold 3. Si TIR &lt; K, el proyecto debe rechazarse debido a que no cubre la rentabilidad mnima esperada.
      .row.justify-content-center(titulo="CAUE")
        .col-md-10.BG16.p-4.mb-4.mb-md-0
          h4 CAUE
          p Indica Costo Anual Uniforme Equivalente llamado tambin flujo final de caja y representa la cantidad de flujos de caja que el proyecto de inversin espera recibir por un nmero n de periodos a una tasa de inters transformando todos lo ingreso y egresos en pagos uniformes o anualidades.
          p <span style="background-color:#FCC15A !important; font-weight: bold; padding: 5px">CAUE = - I0 +(F/(1 + i)n) + (F/(1 + i)n) + (F/(1 + i)n) donde i = TIO</span>
          p Los criterios para aceptar o rechazar un proyecto de inversin bajo este indicador es:
          p.text-bold 1. Si el CAUE es positivo, los ingresos son mayores que los egresos y genera beneficios y rentabilidad.
          p.text-bold 2. Si el CAUE es negativo, los ingresos son menores que los egresos y el proyecto genera prdidas.
      .row.justify-content-center(titulo="ROI")
        .col-md-10.BG16.p-4.mb-4.mb-md-0
          h4 Periodo de Recuperacin de la Inversin - ROI
          p Determina el nmero de periodos o tiempo expresado en n cual fuere los perodos en el flujo de caja, que pueden ser mensuales, bimensuales, trimestrales, semestrales anuales requeridos o necesarios para recuperar la inversin, en otras palabras, compara la inversin inicial con los flujos de caja del proyecto en valores absolutos sin la intervencin de una tasa de inters. Donde I0 corresponde a la inversin inicial y F corresponde a la sumatoria de los flujos de caja obtenidos durante el horizonte del proyecto de inversin.
          p.text-bold ROI = ln / F
      .row.justify-content-center(titulo="TIO")
        .col-md-10.BG16.p-4.mb-4.mb-md-0
          h4 TIO
          p Tasa de Inters de Oportunidad (TIO), corresponde a la tasa o beneficio que un inversionista espera recibir por invertir su dinero en x proyecto y dejar de invertirlo en otro que podra hipotticamente generarle mayores beneficios.
      .row.justify-content-center(titulo="ANF")
        .col-md-10.BG16.p-4.mb-4.mb-md-0
          h4 Activo Neto Financiado (ANF)
          p Tambin conocido como Capital Invertido Neto (CIN), son los activos que han sido financiados con servicio a la deuda representados en pasivos costosos o con recursos del patrimonio, siendo el patrimonio el recurso  donde el costo se hace ms elevado debido a que los inversionistas siempre esperan un beneficio mayor a cualquiera que reciba un acreedor. 
          p <span style="background-color:#FCC15A !important; font-weight: bold; padding: 5px">ANF = patrimonio + pasivos con costo</span>
          p Al identificar los pasivos con costo se refiere a las obligaciones financieras cuyo costo corresponde al servicio a la deuda o gastos financieros a pagar por este servicio, entre los cuales se encuentran obligaciones financieras de corto y largo plazo, los bonos, los pasivos con socios.

    .row.mb-4(data-aos="fade-right")
      .col-auto.BG03.p-3
        h4.mb-0 Costo Promedio de Capital (CPC)
    
    p Se define con el fin de determinar el costo del patrimonio siendo los dueos quienes fijan la tasa de retorno superior a la fijada por los acreedores. Para calcularlo se toma el costo de cada pasivo costoso y la tasa de oportunidad (TIO) esperada por los socios, luego cada rengln se pondera por su respectiva participacin del total de recursos costosos. Para calcularlo se va a tomar la informacin del estado de situacin financiera de Saloan:

    .titulo-sexto.color-acento-contenido.mb-4(data-aos="fade-right")
      h5 Tabla 6
      span Costo promedio de capital

    table.mb-4(data-aos="fade-right")
