    .row.justify-content-center.align-items-center.mb-4
      .col-md-5.col-8.mb-md-0.mb-4
        figure.mb-5(data-aos="fade-right")
          img(src='@/assets/curso/tema3/img22.png')
      .col-md-7.col-10
        p Para calcular la TIO primero se investig acerca de la tasa efectiva anual que cobran actualmente las entidades financieras siendo esta de 26,82 % como la tasa de los socios debe ser superior se fija en 2,18 puntos por encima, luego se calcula el promedio realizando la sumatoria de las tasas y dividiendo en 3 que son el nmero de recursos registrados.  
        p Para calcular la participacin del recurso se divide el valor de cada recurso sobre el valor total convirtiendo a porcentaje ese resultado, la sumatoria de la participacin debe ser igual a 100 %.
        p La ponderacin se obtiene de multiplicar la tasa de cada recurso por su participacin esto nos arroja un nuevo porcentaje o ponderacin.
        p La interpretacin de este indicador se facilita cuando hay dos o ms alternativas de inversin donde se aplique y se defina cul de ellas es la menos costosa para la organizacin. Sin embargo, este componente o indicador individual aporta al clculo del EVA que se estar tratando ms adelante.

    SlyderA(tipo="b")
      .row.justify-content-center
        .col-md-10.col-11.BG17.p-4.mx-4
          h4 Utilidad Neta Ajustada (UNA)
          p Se refiere a la utilidad antes de impuestos y de gastos financieros. Lo que se pretende es definir si esta utilidad neta ajustada es suficiente para atender el costo de los recursos en pasivos, patrimonio y aportar a la generacin de riqueza en la organizacin. Para su clculo se necesita:
          p <span style="background-color:#FCC15A !important; font-weight: bold; padding: 5px">UNA = Utilidad neta + Gastos financieros</span>
      .row.justify-content-center
        .col-md-10.col-11.BG17.p-4.mx-4
          h4 EVA
          p Quiere decir valor econmico agregado, es un indicador creado para crear riqueza basado en la eficiencia y productividad de los bienes y derechos de la empresa o activos, la estructura del capital y el medio al que la empresa pertenece. Su frmula es:
          p <span style="background-color:#FCC15A !important; font-weight: bold; padding: 5px">EVA = UNA - (ANF x CPC)</span>
      .row.justify-content-center
        .col-md-10.col-11.BG17.p-4.mx-4
          h4 EVA: Economic Value Added (valor econmico agregado).
          h4 UNA: Utilidad Neta Ajustada.
          h4 ANF: Activo Neto Financiado.
          h4 CPC: Costo Promedio de Capital.

    p Su fin es evaluar las estrategias a implementar tendientes a aumentar el valor de la empresa basado en los objetivos estratgicos, operacionales y financieros, los recursos invertidos, el riesgo y el beneficio a obtener.
    p A continuacin, se desarrolla un ejemplo para ampliar la aplicacin de indicadores y su interpretacin:     

    .row.justify-content-center.mb-4
      .col-md-10.BG18.p-4
        p La empresa Saloan ha decidido tecnificar an ms su proceso productivo y ha determinado que realizar una inversin en nueva tecnologa para el proceso productivo en el ao 3 con el fin de reducir los costos de produccin, incrementar sus ventas, ser ms productiva y generar riqueza.
      .col-md-5.col-6.px-0(data-aos="fade-right")
        img(src='@/assets/curso/tema3/img23.png')
      .col-md-5.col-8.BG01.p-4(data-aos="fade-left")
        p.mb-0 El escenario actual de la empresa es:
        p.mb-0 #[strong Ventas]: 3333 unidades/ao
        p.mb-0 #[strong Precio de venta]: $15.000
        p.mb-0 #[strong Costo de produccin]: $12.000
        p.mb-0 #[strong Depreciacin]: $12'000.000
        p.mb-0 #[strong Gastos de administracin]: 15 % sobre ventas
        p.mb-0 #[strong Inversin]: $100'000.000
        p.mb-0 #[strong Recursos propios]: el 60 % de la inversin
        p.mb-0 #[strong Momento de la inversin]: ao 3
        p.mb-0 A partir del ao 4 se reducen los costos de produccin en 50 %
        p.mb-0 #[strong Valor en libros]: $4'000.000
        p.mb-0 #[strong Venta de activo]: $8'000.000
        p.mb-0 Incremento de ventas a partir del ao 4 en 50 %
        p.mb-0 #[strong Impuesto]: 35 %
        p.mb-0 #[strong Prstamo]: $40'000.000
        p.mb-0 #[strong i]: 26,82 % E.A.
        p.mb-0 #[strong n]: 4 aos
        p.mb-0 #[strong A]: $xxx? Anual
        p.mb-0 #[strong TIO]: 29 %
        p.mb-0 #[strong Hallar]: VPN, TIR y Serie de cuotas anuales y conclusin.
        
    p Para desarrollar el ejercicio se debe elaborar la tabla de amortizacin de la obligacin. En este caso no se requiere realizar la conversin de tasa de inters porque la cancelacin se realizar con 1 cuota anual durante 4 aos; por tanto, se utiliza una tasa del 26,82 % EA.
