          p.mb-0.text-bold VPN o VAN
          p.mb-0.text-bold VPN = - I0 + (F/(1 + i)n) + (F/(1 + i)n) + (F/(1 + i)n) + 
          p.mb-0.text-bold VPN = - 100.000.000 + (-14.989.327,31/(1 + ,29)1) + (-14.989.327,32/(1 + .29)2) + (-66.989.327,31/(1 + i)3) + (30.006.172,66/(1 + ,29)4) +(47.495.250/(1 + ,29)5)
          p.mb-0.text-bold VPN = - 100.000.000 + (-11.619.633,57) + (-9.007.467.89) + (-31.205.588,71) + (10.835.570)+(13.294.566,9)
          p.mb-0.text-bold VPN = -127.702.553,3 
          p Segn el indicador de VPN en el escenario que se plantea el proyecto y con una TIO de 29 % en un horizonte de 5 aos se debe rechazar el proyecto, si se revisa el flujo de caja la empresa se debe enfrentar a otras fuentes de financiacin para ejecutar este proyecto, pero los resultados propuestos, segn el flujo de caja son apropiados, es decir, el incremento en las ventas y la reduccin de costos pero el proyecto en actuales condiciones requiere un horizonte ms amplio para arrojar resultados beneficiosos para la empresa.
